<template>
  <div id="front">
    <div class="header">
      <div class="header__upperpanel"></div>
      <div class="header__lowerpanel"></div>
      <div class="header__container">
        <div class="header__logo">
          <a href="#" @click.prevent="go_page('')">
            <img src="../assets/header_logo.png" alt="">
          </a>
        </div>
        <div class="header__cart">
          <a href="#">
            <img src="../assets/icon/cart.png" alt="">
            <img src="../assets/icon/cart_hover.png" alt="" class="hovershow">
          </a>
        </div>
        <div class="header__hamburger">
          <a href="#" @click.prevent='toggle_category()'>
            <img src="../assets/icon/main_menu_hamburger.png"
            alt="" class="header__hamburger__folded">
            <img src="../assets/icon/close_menu.png" alt="" class="header__hamburger__cross">
          </a>
        </div>
        <div class="header__nav">
          <div class="header__nav__leftArrow">
            <a href="#" @click.prevent='animate_scroll("header__nav", -1)'>
              <img src="../assets/icon/main_menu_arrow_left.png" alt="">
            </a>
          </div>
          <ul class="header__nav__list"
          @mouseenter="bind_scroll_event('header__nav')"
          @mouseleave="unbind_scroll_event()"
          >
            <li class="header__nav__list__item"
            v-for="(item, index) in database.header_nav"
            :key="index"
            >
              <a href="#"
              @click.prevent="go_page(`front/${item.path}`)">
                <span class="chinese">{{item.chinese}}</span>
                <span class="english">{{item.english}}</span>
              </a>
            </li>
          </ul>
          <div class="header__nav__rightArrow">
            <a href="#" @click.prevent='animate_scroll("header__nav", 1)'>
              <img src="../assets/icon/main_menu_arrow.png" alt="">
            </a>
          </div>
          <div class="header__nav__borderbottom"></div>
        </div>
        <ul class="header__sharelinks">
          <li class="header__sharelinks__item">
            <a :href="database.contact_info.facebook.link">
              <img src="../assets/icon/main_menu_fb.png" alt="">
              <img src="../assets/icon/main_menu_fb_hover.png" alt="" class="hovershow">
            </a>
          </li>
          <li class="header__sharelinks__item">
            <a :href="database.contact_info.instagram.link">
              <img src="../assets/icon/main_menu_ig.png" alt="">
              <img src="../assets/icon/main_menu_ig_hover.png" alt="" class="hovershow">
            </a>
          </li>
          <li class="header__sharelinks__item">
            <a :href="database.contact_info.line.link">
              <img src="../assets/icon/main_menu_line.png" alt="">
              <img src="../assets/icon/main_menu_line_hover.png" alt="" class="hovershow">
            </a>
          </li>
        </ul>
      </div>
    </div>
    <div class="banner">
      <img src="../assets/a8725cb076653957a52d4079b6465679.jpg" alt="">
    </div>
    <div class="category">
      <div class="category__leftArrow">
        <a href="#" @click.prevent='animate_scroll("category", -1)'>
          <img src="../assets/icon/main_menu_arrow_left.png" alt="">
        </a>
      </div>
      <ul class="category__list"
      @mouseenter="bind_scroll_event('category')"
      @mouseleave="unbind_scroll_event()">
        <li class="category__list__item">
          <a href="#"
          @click="go_page(`front/onlinestore/all`)">
            全部
          </a>
        </li>
        <li class="category__list__item"
        v-for="(category, index) in database.product_category"
        :key="index">
          <a href="#"
          @click="go_page(`front/onlinestore/${category}`)">
            {{category}}
          </a>
        </li>
      </ul>
      <div class="category__rightArrow">
        <a href="#" @click.prevent='animate_scroll("category", 1)'>
          <img src="../assets/icon/main_menu_arrow.png" alt="">
        </a>
      </div>
    </div>
    <router-view
    @move_borderbottom='move_borderbottom()'
    :products='products'
    :contact_info='contact_info'></router-view>
    <div class="contact">
      <div class="contact__container">
        <div class="contact__map">
          <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3615.5339428260017!2d121.30752811537842!3d25.015947745168063!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x34681e4cead0ae85%3A0x59887e49e8aeb76d!2z5pel6Z2S5pa55aW9!5e0!3m2!1szh-TW!2stw!4v1606898677103!5m2!1szh-TW!2stw" width="600" height="450" frameborder="0" style="border:0;" allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
        </div>
        <ul class="contact__contactinfo">
          <li class="contact__contactinfo__item">
            <img src="../assets/location_logo.png" alt="" class="contact__contactinfo__logo">
          </li>
          <li class="contact__contactinfo__item telAndLine">
            <a :href="database.contact_info.tel.link">{{database.contact_info.tel.text}}</a>
            <a :href="database.contact_info.line.link">{{database.contact_info.line.text}}</a>
          </li>
          <li class="contact__contactinfo__item">
            <a :href="database.contact_info.mail.link">{{database.contact_info.mail.text}}</a>
          </li>
          <li class="contact__contactinfo__item">
            {{database.contact_info.address.text}}
          </li>
        </ul>
      </div>
    </div>
    <div class="copyright">
      COPYRIGHT GOODAFTER © ALL RIGHTS RESERVED.
    </div>
    <div class="backToTop">
      <a href="#" @click.prevent='go_top()'>
        <img src="../assets/icon/top.png" alt="">
      </a>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  watch: {
    $route() {
      document.title = 'GOODAFTER 日青方好';
    },
  },
  props: ['products', 'contact_info'],
  data() {
    return {
      database: {
        header_nav: [
          // {
          //   path: '',
          //   chinese: '',
          //   english: '',
          // },
        ],
        contact_info: {
          // facebook: {
          //   link: '',
          //   text: '',
          // }.
        },
        product_category: [
          // '',
        ],
      },
    };
  },
  methods: {
    initialize_database() {
      const vm = this;
      const copyRoutes = JSON.parse(JSON.stringify(vm.$router.options.routes));
      const copyProductArray = JSON.parse(JSON.stringify(vm.products));
      const copyContactObj = JSON.parse(JSON.stringify(vm.contact_info));

      // header_nav
      const routeFrontArray = copyRoutes
        .find((route) => route.name === 'Front');
      routeFrontArray.children.forEach((child) => {
        const tempObj = {};
        tempObj.path = child.path;
        tempObj.chinese = child.chinese;
        tempObj.english = child.english;
        if (child.on_header_nav) {
          vm.database.header_nav.push(tempObj);
        }
      });

      // contact_info
      vm.database.contact_info = copyContactObj;

      // product_category
      const categoryArray = copyProductArray.map((product) => product.category);
      const categoryArrayFlat = categoryArray.flat(9);
      vm.database.product_category = categoryArrayFlat
        .filter((element, index, arr) => arr.indexOf(element) === index);
    },
    go_page(pageString) {
      this.$router.push(`/${(pageString === 'front/onlinestore') ? 'front/onlinestore/all' : pageString}`);
    },
    animate_slide_fadein(elementString) {
      const element = document.querySelector(`.${elementString}`);
      $(element).toggleClass('animate-slide-hidden');
    },
    toggle_category() {
      $('#front').toggleClass('header__nav--open');
    },
    toggle_arrow(elementString) {
      const leftArrow = document.querySelector(`.${elementString}__leftArrow`);
      const rightArrow = document.querySelector(`.${elementString}__rightArrow`);
      const list = document.querySelector(`.${elementString}__list`);
      const listLeft = $(list).scrollLeft();
      const listRight = listLeft + $(list).width();
      const items = document.querySelectorAll(`.${elementString}__list__item`);
      let itemsWidthSum = 0;
      items.forEach((item) => {
        itemsWidthSum += $(item).outerWidth(true);
      });
      itemsWidthSum = Math.floor(itemsWidthSum);
      if (listLeft <= 0) {
        $(leftArrow).fadeOut();
      } else {
        $(leftArrow).fadeIn();
      }
      if (listRight >= itemsWidthSum) {
        $(rightArrow).fadeOut();
      } else {
        $(rightArrow).fadeIn();
      }
    },
    animate_scroll(elementString, directionNum) {
      const element = document.querySelector(`.${elementString}__list`);
      if (directionNum > 0) {
        // 向右移動
        $(element).animate({
          scrollLeft: '+=50',
        }, 'fast', () => {
          this.toggle_arrow(elementString);
        });
      } else if (directionNum < 0) {
        // 向左移動
        $(element).animate({
          scrollLeft: '-=50',
        }, 'fast', () => {
          this.toggle_arrow(elementString);
        });
      }
    },
    bind_scroll_event(elementString) {
      $(window).bind('mousewheel', (e) => {
        // deltaY 正往右，負往左
        if (e.originalEvent.deltaY > 0) {
          this.animate_scroll(elementString, 1);
        } else if (e.originalEvent.deltaY < 0) {
          this.animate_scroll(elementString, -1);
        }
      });
    },
    unbind_scroll_event() {
      $(window).unbind('mousewheel');
    },
    go_top() {
      $('html').animate({ scrollTop: '0' }, 500);
    },
    move_borderbottom() {
      // create an array with obj like
      // {
      //   path: '',
      //   left: '',
      //   width: '',
      // }
      const targetArray = [];
      const vm = this;
      const headerNavArray = JSON.parse(JSON.stringify(vm.database.header_nav));
      const liArray = document.querySelectorAll('.header__nav__list__item');
      for (let index = 0; index < headerNavArray.length; index += 1) {
        const tempObj = {};
        tempObj.path = headerNavArray[index].path;
        tempObj.left = liArray[index].offsetLeft;
        tempObj.width = $(liArray[index]).width();
        targetArray.push(tempObj);
      }

      // get current path
      const currentPath = this.$router.history.current.path;
      // find which item in targetArray matches current path
      const currentPathIndex = targetArray
        .findIndex((item) => currentPath.includes(item.path));
      // get borderbottom initial left
      const headerNavWidth = $('.header__nav').width();
      const headerNavListWidth = $('.header__nav__list').width();
      const borderBottomInitialLeft = headerNavWidth - headerNavListWidth - 15;
      // rendor
      $('.header__nav__borderbottom').css({
        left: `${borderBottomInitialLeft + targetArray[currentPathIndex].left}px`,
        width: `${targetArray[currentPathIndex].width}px`,
      });
    },
  },
  created() {
    this.initialize_database();
  },
  mounted() {
    document.title = 'GOODAFTER 日青方好';
    this.move_borderbottom();
    const category = document.querySelector('.category');
    const categoryList = document.querySelector('.category__list');
    let windowWidth = $(window).width();

    this.toggle_arrow('header__nav');
    this.toggle_arrow('category');

    $(window).resize(() => {
      windowWidth = $(window).width();
      if (windowWidth >= 1024) {
        $(category).removeClass('d-none');
        $(categoryList).removeClass('animate-slide-hidden');
        this.move_borderbottom();
      } else if (windowWidth < 1024) {
        this.toggle_arrow('header__nav');
        this.toggle_arrow('category');
      }
    });
  },
};
</script>
